@import "../../global.scss";

.menu {
  width: 300px;
  height: 100vh;
  background-color: $mainColor;
  position: fixed;
  top: 0;
  right: -300px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 1s ease;

  @media (max-width: 812px) {
    z-index: 3;
  }

  &.active {
    right: 0;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 40px;
    font-weight: 200;
    color: #ffff;
    width: 60%;
    li {
        margin-bottom: 20px;

        a {
            color: inherit;
            font-size: inherit;
            text-decoration: none;
          }

          &:hover {
              font-weight: 500;
          }
    }
  }
}
